:root
{
    --bgg:dodgerblue;
    --tc:white;
}
.controlls {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-align-items: center;
            align-items: center;
    background: linear-gradient(-90deg,transparent 12%, #444 40%, transparent 90%);
    width: 94%;    padding: 1rem;
    border-radius: 4px;
}
body, html {
    height: 96%;
    font-family: cursive;
    margin: 10px;
    background: #111;
    color: white;
    color: var(--tc);
}
.xo{
    transition: 0.2s linear;
}
.www{ 
    color: dodgerblue; 
    color: var(--bgg);
    text-shadow: 0px 0px 20px;
}
.xxx{ 
    text-shadow: 0px 0px 20px;
}

.Taker {
    position: relative;
    padding: 0.4rem;
    border-radius: inherit;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    padding-top: 0;
}

.containerTC {
    background: #222;
    text-align: center;
    width: 78px;
    border-radius: 3px;    padding: 0.3rem 1rem;
}
.headsup {
    background: linear-gradient(-90deg,transparent 12%, dodgerblue 40%, transparent 90%);
    background: linear-gradient(-90deg,transparent 12%, var(--bgg) 40%, transparent 90%);
    text-align: center;
    border-radius: 0px 14px 14px 0px;
    padding: 0.2rem 1.5rem 0.2rem 2rem;
    height: 58px;
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: center;
            justify-content: center;
    font-size: -webkit-xxx-large;
    margin: 9px 0px;
    transition: 0.5s linear;
    opacity: 0.86;
}
.sw {
    height: 22px;
    text-align: center;
    width: 21px;
    background: dodgerblue;
    background: var(--bgg);
    border-radius: inherit;
    font-weight: 900;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-evenly;
            justify-content: space-evenly;
    -webkit-align-content: center;
            align-content: center;
    -webkit-align-items: center;
            align-items: center;
    -webkit-align-self: center;
            align-self: center;
    cursor: pointer;
}
.play {
    background: dodgerblue;
    background: var(--bgg);
    margin-top: 10px;
    cursor: pointer;
    width: 60%;
}
.fw {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-evenly;
            justify-content: space-evenly;
    width: 50%;
}
ul.horz>li {
    float: left;
    margin: 8px;
    padding: 0.3rem;
    border-radius: 4px;
    list-style: none;
    height: 20px;
    transition: 0.5s;
    -webkit-animation: app 0.5s linear  1  ;
            animation: app 0.5s linear  1  ;
    background: #222
}
@-webkit-keyframes app{
    from{
        -webkit-transform: scale(0);
                transform: scale(0);
    }
    to{
        -webkit-transform: scale(1);
                transform: scale(1);
    }
}
@keyframes app{
    from{
        -webkit-transform: scale(0);
                transform: scale(0);
    }
    to{
        -webkit-transform: scale(1);
                transform: scale(1);
    }
}
